* {
  box-sizing: border-box;
  margin: 0;
}





.main {
  background: linear-gradient(lightblue, whitesmoke);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.start--head {
  width: 412px;
  height: 41px;

  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 31.25px;
  line-height: 37px;
  text-align: center;
  margin-bottom: 7px;

  color: #293264;
}

.start--text {
  width: 318px;
  height: 19px;
  
  margin-top: 7px;
  margin-bottom: 29px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;

  color: #293264;
}

.start--btn {
  width: 193px;
  height: 52px;
  margin-top: 29px;
  background: #4D5B9E;
  border-radius: 15px;
  cursor: pointer;
  color: aliceblue;
}




.qbox {
    display: flex;
    flex-direction: column;
    margin-bottom: 31px;
    width: 600px;
}


.q--text {
    /* width: 300; */
    height: 35.74px;
    left: 75px;
    top: 121.63px;

    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;

    color: #293264;
    margin-bottom: 11px;
}


.a--div {
    box-sizing: border-box;
    left: 312px;
    top: 433.98px;

    display: flex;
    justify-content: space-evenly;


}


.a--btn {
    border: 0.794239px solid #4D5B9E;
    border-radius: 7.94239px;

    padding: 3px 10px;

    

    color: #293264;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 10.24px;
    line-height: 12px;

    text-align: center;
    cursor: pointer;
}


.a--btn-selected {
    left: 75px;
    top: 353px;
    
    background: #D6DBF5;
    border-radius: 7.71045px;

    color: #293264;

    padding: 3px 10px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 10.24px;
    line-height: 12px;

    text-align: center;
}

.a--btn-correct {
  left: 75px;
  top: 353px;
  
  background: #00ff9d;
  border-radius: 7.71045px;

  color: #036300;

  padding: 3px 10px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 10.24px;
  line-height: 12px;

  text-align: center;
}

.a--btn-incorrect {
  left: 75px;
  top: 353px;
  
  background: #f80000;
  border-radius: 7.71045px;

  color: #000000;

  padding: 3px 10px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 10.24px;
  line-height: 12px;

  text-align: center;
}



.test--page {
  display: flex;
